import React from 'react'
import {NavBar,Icon,TabBar, ActionSheet} from 'antd-mobile';
import {connect} from 'react-redux';
class DetailExample extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			selectedTab: 'redTab',
		  }
		
	  }	
	  componentDidMount(){
		let id=this.props.location.id
		console.log(this.props.todo);
		this.props.todo.map((item)=>{
			if(item.id==id){
				this.setState({
					selectedTab:'blueTab',
				})
			}
		})
	}
    render(){
		const {pathname}=this.props.location;
		let img=this.props.location.src
		let id=this.props.location.id
		let title=this.props.location.title
		console.log(this.props);
       return(
            <div>
                 <NavBar mode="light" icon={<Icon type="left" />} onLeftClick={() => this.props.history.push('/')}>商品详情</NavBar>
     			{/* <img src='https://gw3.alicdn.com/bao/uploaded/i8/TB1gf9emL5TBuNjSspcYXHnGFXa_M2.SS2_210x210.jpg' 
                     alt=""
                     style={{width:'100%',height:'60%'}}/> */}

					<img  src={this.props.location.src}   style={{width:'100%',height:'60%'}}/>
					<span>{this.props.location.title}</span>
					 
				<div style={{ position: 'fixed',  width: '100%', bottom: 0 }}>
				<TabBar
			unselectedTintColor="#949494"
			tintColor="#33A3F4"
			barTintColor="white"
		  >
			<TabBar.Item
			  title="店铺"
			  icon={
				  <i className='iconfont icon-dianpu'></i>
			  }
			selectedIcon={
				<i className='iconfont icon-dianpu'></i>
			}			 			  
			  data-seed="logId"
			>			 
			</TabBar.Item>
			<TabBar.Item
			  icon={
				<i className='iconfont icon-kefu'></i>
			  }
			  selectedIcon={
				<i className='iconfont icon-kefu'></i>
			  }
			  title="客服"			 
			  data-seed="logId1"
			>
			  客服
			</TabBar.Item>
			<TabBar.Item
			  icon={
				<i className='iconfont icon-shoucang1'></i>
			  }
			  selectedIcon={
				<i className='iconfont icon-star__easyico'></i>
			  }
			  title="收藏"	
			  key="shoucang"
			  selected={this.state.selectedTab==='blueTab'}
			  onPress={() => {
				  
				this.props.dispatch({
					type:'ADD_TODO',
					col:{img:img,id:id,title:title}
				})

			this.setState({
					  selectedTab:'blueTab',
				  })
			  }}	 
			>
			 
			</TabBar.Item>
			<TabBar.Item 
			icon={
				 <button style={{width:'100px',height:'35px',backgroundColor:'rgb(255,175,1)',color:'white',borderRadius:'40px 0px 0px 40px',border:'none'}}>加入购物车</button>
			}
			>					
			</TabBar.Item>
			<TabBar.Item 
			icon={
				 <button style={{width:'100px',height:'35px',backgroundColor:'rgb(254,91,8)',color:'white',borderRadius:'0px 40px 40px 0px',border:'none'}}>立即购买</button>
			}
			>								
			</TabBar.Item>
		  </TabBar>			 
				</div>	  
            </div>
        )
       
    }

}

const mapStateToProps = (state) =>({
    todo:state.shoucang
})
export default connect(mapStateToProps)(DetailExample)
//export default DetailExample