import React from 'react';
import {Button,TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom';
class Mytab extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		selectedTab: 'blueTab',
	  };
	}
	render() {
		const {pathname}=this.props.location;
		if(pathname.split('/').length>2){
            return null
        }
	  return (
		<div style={{ position: 'fixed',  width: '100%', bottom: 0 }}>
		  <TabBar
			unselectedTintColor="#949494"
			tintColor="#ff5000"
			barTintColor="white"
		  >
			<TabBar.Item
			  title="首页"
			  key="home"
			  icon={
				  <i className='iconfont icon-shouye'></i>
			  }
			selectedIcon={
				<i className='iconfont icon-shouye'></i>
			}
			 
			  selected={pathname === '/'}
			  onPress={() => {
				
				this.props.history.push('/')
			  }}
			  data-seed="logId"
			>
			 
			</TabBar.Item>
			<TabBar.Item
			  icon={
				<i className='iconfont icon-shoucang1'></i>
			  }
			  selectedIcon={
				<i className='iconfont icon-star__easyico'></i>
			  }
			  title="收藏"
			  key="Login"
				  selected={pathname === '/shoucang'}
				  onPress={() => {
					// this.setState({
					//   selectedTab: 'redTab',
					// });
					this.props.history.push('/shoucang')
				  }}
				  data-seed="logId1"
			
			>
			 
			</TabBar.Item>
			<TabBar.Item
			  icon={
				<i className='iconfont icon-dingdan'></i>
			  }
			  selectedIcon={
				<i className='iconfont icon-dingdan'></i>
			  }
			  title="订单"
			  key="Friend"
			  selected={pathname === '/Friend'}
			  onPress={() => {
				// this.setState({
				//   selectedTab: 'greenTab',
				// });
				this.props.history.push('/Friend');
			  }}
			>
			  订单
			</TabBar.Item>
			<TabBar.Item
			  icon={
				  <i className='iconfont icon-wode'></i>
				}
			  selectedIcon={
				<i className='iconfont icon-wode'></i>
				}
			  title="我的淘宝"
			  key="my"
			  selected={pathname === '/My'}
			  onPress={() => {
				// this.setState({
				//   selectedTab: 'yellowTab',
				// });
				this.props.history.push('/My');
			  }}
			>
			  我的淘宝
			</TabBar.Item>
		  </TabBar>
		</div>
	  );
	}
  }
  export default withRouter(Mytab);