import { render } from '@testing-library/react'
import React from 'react'
import { NavBar, Icon } from 'antd-mobile';
import './nav.css';
import { Carousel, WingBlank } from 'antd-mobile';
import GoodsExample from './dataList';
import { connect } from 'react-redux'
class Home extends React.Component{
  constructor(props) {
	  super(props);
	  
	}
  componentDidMount(){
    this.props.dispatch(GoodsExample())
}
  render(){
    return(
      <div>
        <NavBar style={{ backgroundColor:'rgb(255,133,42)',height:'35px' }}
      mode="dark"
      leftContent="淘"
      rightContent={[
        <button class="search" onClick={()=>this.props.history.push('/home/searchBar')} > 寻找宝贝店铺  </button>
      ]}  
    ></NavBar>
    <CarouselDemo/>
        <div className="goods">
          {this.props.goods.map((item)=>{
            return(
            <div style={{width:'100%'}}>{
               <div style={{width:'45%',margin:'8px 5px',float:'left',backgroundColor:'rgb(242,242,242)' }}  
               onClick={()=>this.props.history.push({ pathname:'home/details',
              src:item.img,id:item.id,title:item.title})} >
                <img  src={item.img} style={{width:'170px',height:'150px',float:'left',marginLeft:'10px'}}/>
                     
                     <br></br>
                     <span style={{color:'black',marginLeft:'5px',paddingBottom:'100px'}}>{item.title}</span>
                     <span style={{color:'rgb(255,85,0)',marginLeft:'10px'}}>￥{item.price}</span>          
                </div>                     
            }           
          </div>            
            )
          })}
        </div>
    </div>    
    )
  }
}
class CarouselDemo extends React.Component {
    state = {
      data: ['1', '2', '3'],
      imgHeight: 176,
    }
    componentDidMount() {
      // simulate img loading
      setTimeout(() => {
        this.setState({
          data: ['https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg', 'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000001781/O1CN01Qh4p1P1P1k1VQly5t_!!6000000001781-0-octopus.jpg'],
        });
      }, 100);
    }
    render() {
      return (
        <div>
          <Carousel
            autoplay={true}
            infinite
            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
            afterChange={index => console.log('slide to', index)}
          >
            {this.state.data.map(val => (
              <a
                key={val}
                href="http://www.alipay.com"
                style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
              >
                <img
                  src={`${val}`}
                  alt=""
                  style={{ width: '100%', verticalAlign: 'top' }}
                  onLoad={() => {
                    // fire window resize event to change height
                    window.dispatchEvent(new Event('resize'));
                    this.setState({ imgHeight: 'auto' });
                  }}
                />
              </a>
            ))}
          </Carousel>
        </div>
      );
    }
  }
  


  const mapStateToProps = (state)=>{
    return {
        goods:state.datalist.goods
    }
}
export default connect(mapStateToProps)(Home)

//export default Home
