import React,{useState} from 'react'
import {connect} from 'react-redux';
const CollectExample = (props) => {
    console.log(props.todo)
    return (
        <div>
            {
               props.todo.map((item,idx)=>
                <li key={item} style={{listStyleType:'none'}}>
                <img src={item.img} style={{width:'100px',height:'100px'}} />
                <span style={{marginTop:'30px'}}>{item.title}</span>
                <button onClick={(e)=>{
                     props.dispatch({
                        type:'DEL_TODO',
                        index:idx
                    })
                }} style={{width:'90px',height:'50px',backgroundColor:'#ff8500',color:'white',border:'0px',borderRadius:'10px 10px 10px 10px'}}>删除</button></li>
               )          
           }
        </div>

    )
}
const mapStateToProps = (state) =>({
    todo:state.shoucang
})
export default connect(mapStateToProps)(CollectExample)















