import React,{useState} from 'react'
import { connect } from 'react-redux'
import {ActivityIndicator} from 'antd-mobile';
import {login} from './actionCreators';
import Collect from './shoucang';
const CollectExample = (props) => {  
    const [isShow,setShow]=useState(false);
    const log = () => {
        props.dispatch(login())
    }
    return (
        <div>
            {                 
                    props.username
                    ? 
                    <div>{
                        localStorage.setItem('userinfo',props.username),
                        props.history.push('/shoucang')
                        }
                        </div>                  
                    : 
                    <div>
                        <img src="https://gw.alicdn.com/tfs/TB1p.BGQXXXXXbFXFXXXXXXXXXX-160-160.png" style={{width:'100px',height:'100px',paddingLeft:'130px',paddingTop:'130px'}} />
                        <input type="text" placeholder="手机号/邮箱/会员名" style={{width:'200px',height:'30px',border:'1px solid #ff8500',marginLeft:'70px',marginTop:'50px'}}/>
                        <input type="text" placeholder="请输入登录密码" style={{width:'200px',height:'30px',border:'1px solid #ff8500',marginLeft:'70px',marginTop:'10px'}}/>
                    <button 
                        onClick={ log }                   
                        style={{width:'300px',height:'50px',backgroundColor:'#ff8500',color:'white',border:'0px',borderRadius:'40px 40px 40px 40px',float:'left',marginLeft:'40px',marginTop:'50px'}}
                    >登录</button> 
                    </div>           
            }              
        </div>
    )
}
const mapStateToProps = (state) => ({
    username: state.userinfo.username
})
export default connect(mapStateToProps)(CollectExample);